@import url(https://fonts.googleapis.com/css2?family=Grandstander:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kufam:wght@400;500&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-image: linear-gradient(45deg, rgb(224, 198, 48) 0%, rgb(238, 159, 55) 80%);
  margin: 0;
  padding: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  /* align-items: center; */
  margin-top: 50px;
  border: 1px solid black;
  background-color: #00BFA6;
  width: 500px;
  min-height: 500px;
  border-radius: 5px;
  box-shadow: 0 0 10px 4px black;
}

.App h1, h3 {
  font-family: 'Grandstander', cursive;
}

h1.appTitle {
  border-bottom: 1px solid rgb(228, 228, 228);
  padding: 10px;
  color: white;
  font-size: 40px;
}

.taskAdd {
  margin-bottom: 20px;
}


input {
    padding: 10px;
    min-width: 250px;
    border: none;
    margin-right: 15px;
    font-family: 'Kufam', cursive;
    outline: none;
}

button.btnAdd {
    padding: 10px 15px;
    border: none;
    text-transform: uppercase;
    font-family: 'Kufam', cursive;
    cursor: pointer;
    background-color: rgb(245, 207, 101);
    transition: background-color .3s, box-shadow .3s;
    box-shadow: 2px 2px 5px 3px black;
    outline: none;
}

.btnAdd:hover {
    background-color: rgb(252, 191, 24);
    box-shadow: 2px 2px 3px 2px black;
}
button {
    background: none;
    border: none;
    cursor: pointer;
}

button i {
    font-size: 16px;
}

.check {
    color: rgb(181, 228, 13);
    transition: color .3s;
}

.check:hover {
    color: rgb(0, 255, 0);
}

.delete {
    color: rgb(219, 101, 101);
    transition: color .3s;
}

.delete:hover {
    color: rgb(228, 43, 43);
}

.list {
    display: flex;
    justify-content: space-between;
}

.btns, .btnDel {
    margin-right: 40px;
}


h1, h3 {
    color: white;
    font-weight: normal;
}

h1 {
    font-size: 24px;
}

h3 {
    font-size: 16px;
}

p {
    margin-top: 40px;
}

.active {
    margin-bottom: 30px;
    padding-bottom: 40px;
    border-bottom: 1px solid rgb(207, 207, 207);
}

.done {
    margin-bottom: 50px;
}

span {
    font-size: 16px;
    margin-left: 40px;
}
